.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	/* background-color: #282c34; */
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	/* color: white; */
}

.App-link {
	/* color: #61dafb; */
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.react-clock__face {
	border-width: 2px !important;
	background: white;
}

.react-clock__mark__number {
	font-weight: bold;
}

.react-clock__minute-hand__body {
	background: #555 !important;
}

.day-night-icon {
	width: min(20vw, 20vh);
	margin-bottom: 2em;
}

.random-button {
	background: #eee;
	border-radius: 8px;
	border: none;
	padding: 1em 1.5em;
	margin-top: 3em;
	color: #888;
  min-width: 10em;
}

.is-night {
	background: linear-gradient(to top, #1a72f2, #160664);
}
.is-day {
	background: linear-gradient(to top, #f8b500, #fceabb);
}
